
"use client";
import { useEffect, useState } from 'react';

export default function ContactsTable({ search = "" }: { search?: string }) {
  const [contacts, setContacts] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    fetch('/api/contacts')
      .then(res => res.json())
      .then(data => {
        setContacts(data);
        setLoading(false);
      });
  }, []);

  if (loading) return <div style={{ padding: 32, textAlign: 'center', color: '#888' }}>Loading contacts...</div>;

  const filtered = search
    ? contacts.filter((c: any) =>
        (c.name || '').toLowerCase().includes(search.toLowerCase()) ||
        (c.email || '').toLowerCase().includes(search.toLowerCase()) ||
        (c.phone || '').toLowerCase().includes(search.toLowerCase())
      )
    : contacts;

  return (
    <div style={{ overflowX: 'auto' }}>
      <table style={{ width: '100%', borderCollapse: 'separate', borderSpacing: 0, fontSize: 16 }}>
        <thead>
          <tr style={{ background: '#f4f5f7', color: '#222', fontWeight: 700 }}>
            <th style={{ padding: '12px 16px', textAlign: 'left', borderTopLeftRadius: 8 }}>Name</th>
            <th style={{ padding: '12px 16px', textAlign: 'left' }}>Email</th>
            <th style={{ padding: '12px 16px', textAlign: 'left' }}>Phone</th>
            <th style={{ padding: '12px 16px', textAlign: 'left', borderTopRightRadius: 8 }}>Account</th>
          </tr>
        </thead>
        <tbody>
          {filtered.length === 0 ? (
            <tr><td colSpan={4} style={{ padding: 32, textAlign: 'center', color: '#888' }}>No contacts found.</td></tr>
          ) : (
            filtered.map((c: any) => (
              <tr key={c.id} style={{ borderBottom: '1px solid #f0f0f0', transition: 'background 0.2s' }}>
                <td style={{ padding: '12px 16px' }}>{c.name}</td>
                <td style={{ padding: '12px 16px' }}>{c.email}</td>
                <td style={{ padding: '12px 16px' }}>{c.phone}</td>
                <td style={{ padding: '12px 16px' }}>{c.accountId}</td>
              </tr>
            ))
          )}
        </tbody>
      </table>
    </div>
  );
}
